const accordionHeaders = document.querySelectorAll('.accordion-header');

accordionHeaders.forEach(header => {
  header.addEventListener('click', () => {
    const accordionItem = header.closest('.accordion-item');
    const iconUse = header.querySelector('.accordion-icon use');
    
    // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ, —á–∏ –º–∞—î –±–∞—Ç—å–∫—ñ–≤—Å—å–∫–∏–π –µ–ª–µ–º–µ–Ω—Ç –∫–ª–∞—Å 'active'
    const isActive = accordionItem.classList.contains('active');
    
    // –ü–µ—Ä–µ–º–∏–∫–∞—î–º–æ –∫–ª–∞—Å 'active'
    accordionItem.classList.toggle('active');

    // –û–Ω–æ–≤–ª—é—î–º–æ –ø–æ—Å–∏–ª–∞–Ω–Ω—è –Ω–∞ —ñ–∫–æ–Ω–∫—É
    if (isActive) {
      // –Ø–∫—â–æ –∞–∫–æ—Ä–¥–µ–æ–Ω –±—É–≤ –∞–∫—Ç–∏–≤–Ω–∏–π (—ñ –º–∞–≤ —Å—Ç—Ä—ñ–ª–∫—É –≤–Ω–∏–∑), —Å—Ç–∞–≤–∏–º–æ —Å—Ç—Ä—ñ–ª–∫—É –≤–≥–æ—Ä—É
      iconUse.setAttribute('href', './public/icons.svg#icon-arrow-up');
    } else {
      // –Ø–∫—â–æ –∞–∫–æ—Ä–¥–µ–æ–Ω –±—É–≤ –∑–∞–∫—Ä–∏—Ç–∏–π, —Å—Ç–∞–≤–∏–º–æ —Å—Ç—Ä—ñ–ª–∫—É –≤–Ω–∏–∑
      iconUse.setAttribute('href', './public/icons.svg#icon-arrow-down');
    }
  });
});